import { Grid } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import PersonAddIcon from '@material-ui/icons/LocalShipping'
import PropTypes from 'prop-types'
import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import { Link, Route } from 'react-router-dom'
import { bindActionCreators } from 'redux'

import styles from '../../../resources/theme/users'
import { fetchFleet } from '../FleetActions'
import { getFleetListData } from '../FleetReducer'
import EditFleet from './Edit'
import DeleteFleet from './Delete'
import List from './List'
import NewFleet from './New'
import Filters from './Filters'

class Fleet extends Component {
  componentDidMount() {
    this.props.fetchFleet()
  }

  render() {
    const { classes, items } = this.props

    return (
      <Fragment>
        <Grid container justify="center" alignItems="center">
          <Grid item className={classes.grow}>
            <Typography variant="h3">Frotistas</Typography>
          </Grid>
          <Grid item>
            <Button
              size="medium"
              color="primary"
              variant="contained"
              component={Link}
              to="/fleet/new"
            >
              <PersonAddIcon className={classes.btnIcon} /> Cadastrar
            </Button>
          </Grid>
        </Grid>

        <Filters />

        <Route path="/fleet/new" component={NewFleet} />
        <Route path="/fleet/:id/edit" component={EditFleet} />
        <Route path="/fleet/:id/delete" component={DeleteFleet} />

        <List classes={classes} items={items} />
      </Fragment>
    )
  }
}

Fleet.propTypes = {
  classes: PropTypes.object.isRequired,
  //fetchFleet: PropTypes.func.isRequired,
  items: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  ...getFleetListData(state),
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
        fetchFleet,
    },
    dispatch
  )
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Fleet))
